<script setup>
import ModalComponent from "@/components/Common/Modal/ModalComponent.vue";
import PageTitleWithGoBackButton from "@/components/LayoutItems/PageTitleWithGoBackButton.vue";
import {ref} from "vue";
import axios from "@/services/api/axios";
import useErrorAlert from "@/composables/useErrorAlert";
import useAlert from "@/composables/useAlert";
import EditWorkerForm from "@/components/EditWorkerForm.vue";
import {onMounted} from "@vue/runtime-core";
import {useRoute} from "vue-router";

const {alertThisError} = useErrorAlert()
const {alertIt} = useAlert()
const route = useRoute()
const modalComponentRef = ref()
const initialFiled = ref()
const onSubmit = (async (formData) => {
  try {
    const {message} = await axios.patch(`/auth/userUpdateForApparat/`+route.params.id + '/', formData)
    await alertIt(message, 'success')
    modalComponentRef.value.openModal()
  } catch (e) {
    alertThisError(e)
  }
});
onMounted( async()=> {
  const res = await axios.get('/auth/users/'+route.params.id)
  initialFiled.value = res.data
})
</script>

<template>
  <div class="bg-white px-[20px] py-[16px]">
    <PageTitleWithGoBackButton>
      Xodim o'zgartirish
    </PageTitleWithGoBackButton>
    <section class="p-2">
      <EditWorkerForm :initial-fields="initialFiled" @submit="onSubmit" />
    </section>
    <ModalComponent
      ref="modalComponentRef"
      :close-on-click-away="false"
      :hide-close-button="true"
      modal-position="center"
      modal-class="!w-auto"
    >
      <template #slotBody>
        <div class="p-10 text-3xl flex items-center justify-center">
          <h1>Xodim markazga muvaffaqiyatli o'zgartirildi</h1>
        </div>
        <div class="flex justify-center">
          <button
            class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80"
            @click="modalComponentRef.closeModal"
          >
            Davom etish
          </button>
          <button
            class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80"
            @click="$router.push('/')"
          >
            Bosh sahifa
          </button>
        </div>
      </template>
    </ModalComponent>
  </div>
</template>

