<template>
  <form @submit="onSubmit">
    <div class="grid grid-cols-3 gap-3 w-full lg:w-[85%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="last_name"> Familiyasi </label>
        <input id="last_name" v-model="last_name" name="last_name" class="base_input" placeholder="Familiyasini kiriting">
        <span class="error_message">{{ last_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="first_name"> Ismi </label>
        <input id="first_name" v-model="first_name" name="first_name" class="base_input" placeholder="Ismini kiriting">
        <span class="error_message">{{ first_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="father_name"> Sharifi </label>
        <input
          id="father_name" v-model="father_name" name="father_name" class="base_input"
          placeholder="Sharifini kiriting"
        >
        <span class="error_message">{{ father_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label"> Tugâ€˜ilgan yili va sanasi </label>
        <DateComponent v-model="birth_date" />
        <span class="error_message">{{ birth_dateError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="login"> Login </label>
        <input id="login" v-model="login" name="login" class="base_input" placeholder="loginni kiriting">
        <span class="error_message">{{ loginError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="father_name"> Parol </label>
        <input id="password" v-model="password" name="password" class="base_input" placeholder="Parolmi kiriting">
        <span class="error_message">{{ passwordError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="login"> Elektron manzil </label>
        <input id="email" v-model="email" name="email" class="base_input" placeholder="Elektron manzilini kiriting">
        <span class="error_message">{{ emailError }}</span>
      </div>
    </div>
    <button type="submit" :disabled="isSubmitting" class="base_input bg-primary text-white border-primary">
      O'zgartirish
    </button>
  </form>
</template>

<script setup>
import { useField, useForm } from 'vee-validate';
import * as yup from 'yup';
import { defineEmits, defineProps, toRefs, watch } from 'vue';
import dayjs from 'dayjs';
import { isLatinLetters, requiredValidatorMessage } from '../utils/validatorMessages';
import DateComponent from './Common/DateComponent';

const { handleSubmit, isSubmitting, resetForm } = useForm();
const emit = defineEmits(['submit']);
const props = defineProps({
  // eslint-disable-next-line vue/require-default-prop
  initialFields: {
    type: Object,
  },
  clearFormMode: {
    type: Boolean,
    default: false,
  },
  isEditMode: {
    type: Boolean,
    default: false,
  },
});
const { clearFormMode } = toRefs(props);

watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);
watch(() => props.initialFields, (value) => {
  console.log(props.initialFields)
  if (props.initialFields) {
    first_name.value = props.initialFields?.first_name
    last_name.value = props.initialFields?.last_name
    father_name.value = props.initialFields?.father_name
    login.value = props.initialFields?.login
    email.value = props.initialFields?.email
    birth_date.value = props.initialFields?.birth_date
  }
}, {
  immediate: true,
  deep: true
})
function onInvalidSubmit({ errors }) {
  console.log(errors);
}

const onSubmit = handleSubmit(async ({ birth_date, ...args }) => {
  const sendObj = {
    birth_date: dayjs(birth_date).format('YYYY-MM-DD'),
  };
  Object.keys(args).map(key => {
    if (args[key]) {
      sendObj[key] = args[key]
    }
  })
  emit('submit', sendObj);
}, onInvalidSubmit);

const { value: first_name, errorMessage: first_nameError } = useField(
  'first_name',
  yup
    .string()
    .test(...isLatinLetters())
    // eslint-disable-next-line no-undef
    .required(requiredValidatorMessage)
);

const { value: last_name, errorMessage: last_nameError } = useField(
  'last_name',
  yup
    .string()
    .test(...isLatinLetters())
    // eslint-disable-next-line no-undef
    .required(requiredValidatorMessage)
);

const { value: father_name, errorMessage: father_nameError } = useField(
  'father_name',
  yup
    .string()
    .test(...isLatinLetters())
    // eslint-disable-next-line no-undef
    .required(requiredValidatorMessage)
);
// eslint-disable-next-line no-undef
const { value: birth_date, errorMessage: birth_dateError } = useField('birth_date', yup.string().required(requiredValidatorMessage), {
  initialValue: new Date(1990, 0, 1),
});
const { value: password, errorMessage: passwordError } = useField(
  'password',
  yup
    .string()
    .test(...isLatinLetters())
);
const { value: login, errorMessage: loginError } = useField(
  'login',
  yup
    .string()
    .test(...isLatinLetters())
    // eslint-disable-next-line no-undef
    .required(requiredValidatorMessage)
);
const { value: email, errorMessage: emailError } = useField(
  'email',
  yup
    .string()
    .email("Email shaklida bo'lishi kerak")
    .test(...isLatinLetters())
    // eslint-disable-next-line no-undef
    .required(requiredValidatorMessage)
);
</script>

<style scoped></style>
